// export default store

import Vuex from 'vuex'
import Vue from 'vue'

import axios from 'axios'

Vue.use(Vuex)

var apiUrl = 'https://igeoserver.herokuapp.com/intersect_feature/v1/qhct/diachinh?geom_column_from=geom&geom_column_to=geom&columns="t"."dientich",%20"f"."maqh",%20"f"."loaidat",%20ST_ASGEOJSON(ST_Transform(ST_SetSRID(ST_Intersection("t"."geom","f"."geom"),%204756%20),4326))%20as%20"kgian"%20,%20ST_Area(ST_Intersection(%20ST_Transform("t"."geom",%203405)%20,ST_Transform(%20"f"."geom",%203405)))%20as%20"DTQH"&distance=0&limit=100';

// var apiUrl = 'https://igeoserver.herokuapp.com/intersect_feature/v1/qhct/diachinh?geom_column_from=geom&geom_column_to=geom&columns="t"."dientich",%20"f"."maqh",%20"f"."loaidat",%20ST_ASGEOJSON(ST_Transform(ST_SetSRID(ST_Intersection(ST_Transform("t"."geom", 3405) , ST_Transform( "f"."geom", 3405)),%203405%20),4326))%20as%20"kgian"%20,%20ST_Area(ST_Intersection(%20ST_Transform("t"."geom",%203405)%20,ST_Transform(%20"f"."geom",%203405)))%20as%20"DTQH"&distance=0&limit=100';

var thuadatUrl = 'https://igeoserver.herokuapp.com/query/v1/diachinh?columns=ST_ASGEOJSON(ST_Transform(ST_SetSRID(%22geom%22%2C4756)%2C4326))%20as%20%22kgian%22&limit=1000'


var store = new Vuex.Store({
  state: {

    pSotos: "",
    pThuaDats: "",
    pMaThua: "",
    showQHCT: false,

    pQHCTs: "",


    chonglop: JSON.parse('{}'),
    diachinh: "",
    qhct: "",
    qhchung: "",
    kgian:  JSON.parse('{"type": "FeatureCollection","features": []}'),

    mapcanvas: "",
  }, 
  mutations: {

    SHOWQHCT (state, value) {   
      state.showQHCT = value;
    },

    QHCTs (state, value) {   
      state.pQHCTs = value;
    },

    SOTO (state, value1) {
      
      state.pSotos = value1;
    },
    THUADAT (state, value2) {   
      state.pThuaDats = value2;
    },
    MATHUA (state, value3){
      state.pMaThua = value3;
    },

    MAPCANVAS (state, value) {
        state.mapcanvas = value;
    },


    DIACHINH (state, value) {
        state.diachinh = value;
    },

    QHCT (state, value) {
        state.qhct = value;
    },

    QHCHUNG (state, value) {
        state.qhchung = value;
    },

    KGIAN (state, value) {

      var thuadatUrl1 = thuadatUrl + '&filter="mathua"%20=%20%27' + value + '%27';

       var jsonColltext = '{"type": "FeatureCollection","features": [';

          var jsontext = '{"type": "Feature","properties": {},"geometry": ';

          var jsonEndText = '}';

          var jsonEndCollText = ']}';

      
      axios.get(thuadatUrl1)
        .then(function (response) {
          // console.log(response.data);
          // state.kgian = response.data;

         
          // for (i = 0; i < response.data.length; i++) { 
          //     if (i <  response.data.length - 1){
          //          jsontext = jsontext + response.data[i].kgian.toString() + jsonEndText + ',';
          //     }else{
          //          jsontext = jsontext + response.data[i].kgian.toString() + jsonEndText;
          //     }

             
          // }

          var i=0;
          response.data.forEach(function(element) {
              i = i + 1;
              // console.log(element);
              if (i < response.data.length - 1 ){
                jsontext = jsontext + element.kgian.toString() + jsonEndText + ',';
              }else{
                jsontext = jsontext + element.kgian.toString() + jsonEndText;
              }
              
          }); 


          var geojsonTong = jsonColltext + jsontext + jsonEndCollText;
          // console.log(geojsonTong)
          state.kgian = JSON.parse(geojsonTong);

        })
        .catch(function (error) {
          console.log(error);
      });
       
    },


    CHONGLOP (state, value) {
        // state.thuoctinh = value;

      var apiUrl1 = apiUrl + '&filter="t"."mathua"%20=%20%27' + value + '%27'

      axios.get(apiUrl1)
        .then(function (response) {
          // console.log(response.data);

          var jsonColltext = '{"type": "FeatureCollection","features": [';

          var jsontext1 = '{"type": "Feature","properties": {';
          var jsontext2 = '},"geometry": ';

          var jsonEndText = '}';

          var jsonEndCollText = ']}';

          var i=0;
          var jsonFeature = '';
          response.data.forEach(function(element) {
              i = i + 1;
              // console.log(element);

              // if (i < response.data.length){
              //   jsonFeature = jsonFeature + jsontext1 + '"dientich:" "' + element.dientich + '", "loaidat:" "' + element.loaidat  + '", "dtqh:" "' + element.DTQH + '"' + jsontext2 + element.kgian.toString() + jsonEndText + ',';
              // }else{
              //   jsonFeature = jsonFeature + jsontext1 + '"dientich:" "' + element.dientich + '", "loaidat:" "' + element.loaidat  + '", "dtqh:" "' + element.DTQH + '"' + jsontext2 + element.kgian.toString() + jsonEndText;
              // }
              if (i < response.data.length){
                jsonFeature = jsonFeature + jsontext1 + '"dientich": "' + element.dientich + '", "loaidat": "' + element.loaidat  + '", "dtqh": "' + element.DTQH + '"' + jsontext2 + element.kgian.toString() + jsonEndText + ',';
              }else{
                jsonFeature = jsonFeature + jsontext1 + '"dientich": "' + element.dientich + '", "loaidat": "' + element.loaidat  + '", "dtqh": "' + element.DTQH + '"' + jsontext2 + element.kgian.toString() + jsonEndText;
              }
              
          }); 


          var geojsonTong = jsonColltext + jsonFeature + jsonEndCollText;
          // console.log(geojsonTong)
          state.chonglop = JSON.parse(geojsonTong);


          // state.chonglop = response.data;
        })
        .catch(function (error) {
          console.log(error);
      });

    },


  },

  getters:{

    zpThuaDats: state  => () => state.pThuaDats,
    zpQHCTs: state  => () => state.pQHCTs,

    zshowQHCT: state  => () => state.showQHCT,

    zmapcanvas: state  => () => state.mapcanvas,
    zkgian: state  => () => state.kgian,
    zchonglop: state  => () => state.chonglop,

    zdiachinh: state  => () => state.diachinh,
    zqhct: state  => () => state.qhct,
    zqhchung: state  => () => state.qhchung,
  }
})

export default store


