export default {
  name: 'pmap',

  mounted: function () {

    this.initMap()
   
    this.addNhaLayer()
    this.overlapLayer()

  },

  methods: {
    initMap () {

        //alert(store.state.zoomlevel);

        L.Icon.Default.imagePath = 'https://unpkg.com/leaflet@1.0.0-rc.3/dist/images'
        mymap = L.map('mapid').setView([10.779633039528331, 106.70273780822752],10)

        baseLayer = L.tileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
          maxZoom: 18
        }).addTo(mymap);

        mymap.zoomControl.setPosition('bottomright');
        

        // L.marker([51.5, -0.09]).addTo(mymap)
        //  .bindPopup('<b>Hello world!</b><br />I am a popup.').openPopup()

        var thecircle = L.circle([10.4418,105.1982], 2400, {
          color: 'red',
          fillColor: '#f03',
          fillOpacity: 0.5
        }).addTo(mymap).bindPopup('I am a circle.')

             

      },




    addNhaLayer() {

      myStyle = {
      fillColor: "#e6b800",
      color: "#e6b800",
      weight: 4,
      opacity: 0.0,
      fillOpacity: 0.8
      };

      function onEachFeature(feature, layer) {
        var popupContent = "<p>I started out as a GeoJSON " +
            feature.geometry.type + ", but now I'm a Leaflet vector!</p>";

        if (feature.properties && feature.properties.popupContent) {
          popupContent += feature.properties.popupContent;
        }

        layer.bindPopup(popupContent);
      }

      var baseballIcon = L.icon({
        iconUrl: 'http://leafletjs.com/examples/geojson/baseball-marker.png',
        iconSize: [32, 37],
        iconAnchor: [16, 37],
        popupAnchor: [0, -28]
      });

      nhaLayer = L.geoJSON(NhageojsonFeature, {

        pointToLayer: function (feature, latlng) {
          return L.marker(latlng, {icon: baseballIcon});
        },

        onEachFeature: onEachFeature
      })

      // L.geoJSON(NhageojsonFeature, {style: myStyle}).addTo(mymap)

    },

    overlapLayer(){
      var baselays = {"Nen": baseLayer, };
      var overlays = {"Nha": nhaLayer};


      L.control.layers(baselays, overlays, {position: 'bottomright'}).addTo(mymap);


  },
  }
}
  

