<template lang="html">

<div id="demo">
  <div class="ui small basic icon buttons" style="float: left; background-color: #f2f2f2; margin-left:20px;">
    <button class="ui button" v-on:click="pOpenFormSearch" ><i class="search icon" style="color: #ce2b37;" ></i></button>
   <!--  <button class="ui button"><i class="info circle icon" style="color: #011efe;"></i></button>
    <button class="ui button"><i class="upload icon"></i></button>
    <button class="ui button"><i class="download icon"></i></button> -->
  </div>

  <div v-if="pOpenSearch" style="margin-left:20px;" >

<!-- TITLE -->  
    <form class="ui form attached fluid segment" style="top:34px;padding-bottom:10px; padding-right:10px;"> 
 
       <i class="close icon" style="position: absolute;right:20px;color: #afafaf;" v-on:click="pCloseFormSearch" ></i>
      <p style="font-size: 22px;text-align: center; font-weight:bold;">Tìm thửa đất</p>
   
     <div id="pThongtin" style="padding-right:10px;height:auto;width:420px;padding-top:10px">

<!-- CONTROL TIMKIEM -->
        <div class="two fields">
        <div class="field" >
          <label>Quận/huyện</label>
          <vue-typeahead      
                    :default-suggestion="true"       
                    :local="pQuans"
                    placeholder="Quận/huyện"
                    classes="form-control1"
                    v-on:selected="SelectedQuan">              
          </vue-typeahead>

        </div>
        <div class="field">
          <label>Phường/xã</label>
          <vue-typeahead      
                      :default-suggestion="true"       
                      :local="pPhuongs"
                      placeholder="Phường/xã"
                      classes="form-control2"
                      v-on:selected="SelectedPhuong">              
            </vue-typeahead>
        </div>
      </div>
      <div class="two fields">
        <div class="field">
          <label>Số tờ</label>
           <vue-typeahead      
                      :default-suggestion="true"       
                      :local = "pSotos"
                      placeholder="Số tờ"
                      classes="form-control3"
                      v-on:selected="SelectedSoTo"
                      v-on:input="UpdateSoTo">              
            </vue-typeahead>
        </div>
        <div class="field">
          <label>Số thửa</label>
          <vue-typeahead      
                      :default-suggestion="true"       
                      :local="pSothuas"
                      placeholder="Số thửa"
                      classes="form-control4"
                      v-on:selected="SelectedSoThua"
                      v-on:input="UpdateSoThua"> 
                                   
            </vue-typeahead>
        </div>
      </div>
      
      <div class="field">
        <div class="ui checkbox" id="checkbox1" @click="disabled=!disabled" v-on:click="pQueryDiaChi">
          <input type="checkbox" >
          <label>Tìm theo Địa chỉ</label>
        </div>
      </div>
      <div class="field" >
        <div style="width:400px;" id="iddc">
          <vue-typeahead      
              :default-suggestion="true"       
              :local="pDiaChifulls"
              placeholder="Địa chỉ"
              classes="form-control5"
              v-on:selected="SelectedDiaChi"
              v-on:input="UpdateDiaChi"
              :disabled="!disabled"
              style="width:400px;"
              >                           
          </vue-typeahead>
        </div>
      </div>
      <div>
        <a class="ui blue submit button" style="margin-bottom:10px;" v-on:click="pQueryDC" href="http://localhost:8080/#/bdqh/kq" >Tìm kiếm</a>
      </div>

<!---->
     </div>     
    </form>
  </div>
    

</div>

</template>
 
 


<script>

import store from '../../src/store'

import { mapGetters } from 'vuex';

import VueTypeahead from 'vuejs-autocomplete';

var allItemsDC;
var pDiaChifulls;
var pDiaChifull;
var pPhuongs;
var pSotos;
var pSothuas;
var pTenQ;
var pTenP;
var pSoto;
var pSothua;
var pCurrentID;
// var disabled;
// var pTestCheck = false;
export default {
   // extends: VueTypeahead,
    name: 'toolbar',
    components:{
        VueTypeahead,
    },
    data() {
      return {
          pOpenSearch: false,
          pOpenResult: false,
          isValid: true,
          pQHC: true,
          allItemsDC: [],
          pCurrentID,
          selected: null,
          typeahead: null,
          pDiaChifulls,
          pDiaChifull,
          pQuans: ['Quận Tân Bình'],
          pPhuongs: [],
          pSotos,
          pSothuas,
          pTenQ,
          pTenP,
          pSoto,
          pSothua,
          disabled: false,
      }
    },
    mounted: function () {
    },

  methods: {

  pOpenFormSearch: function(){
    this.pOpenSearch = true;
    this.pOpenResult = true;
    var self = this;
  },

  pCloseFormSearch: function(){
     this.pOpenSearch = false; 
  },

  pQueryDiaChi:function(){

     var self = this;
  
        $.ajax({
          url: 'http://igeoserver.herokuapp.com/query/v1/diachinh?columns="diachifull" &limit=10000',
          type: 'get',
          dataType: 'json',
          async: false,
          success: function(data) {
          //self.pDiaChis = data;
          var pNewArr2 =  _.map(data, 'diachifull');
          self.pDiaChifulls = _.sortBy(pNewArr2);
          //console.log(self.pDiaChis);
          },
          error: function (error) {
                alert(JSON.stringify(error));
          }
        });
     // $('.typeahead-suggestions').disabled = "disabled";
     //  alert("check");
  },
 

  SelectedDiaChi: function(e){
     pDiaChifull = e;
  },

  UpdateDiaChi: function(e){
     pDiaChifull = e
  },

  SelectedQuan: function(e){
    //console.log(e);
     var self = this;
    self.pPhuongs = ["Phường 2"];
  },

  SelectedPhuong: function(e){
    //console.log(e);
    pTenP = e;
    var self = this;
    var value = "Phường 2";
    $.ajax({
      url: 'http://igeoserver.herokuapp.com/query/v1/diachinh?columns="soto", "tenxa"&filter="tenxa"%20=%20%27' + pTenP+ '%27&limit=10000',
      type: 'get',
      dataType: 'json',
      async: false,
      success: function(data) {
     self.pSotos = data;

      var pNewArr1 = _.uniqBy(data, 'soto');
      var pNewArr2 =  _.map(pNewArr1, 'soto');
      var pNewArr3 = _.map(pNewArr2, _.unary(parseInt));
      self.pSotos = _.sortBy(pNewArr3);

      // store.commit('SOTO', self.pSotos);  

      // alert(JSON.stringify(pNewArr1));
      //  alert(JSON.stringify(pNewArr2));
      //  alert(JSON.stringify(pNewArr3));
       // alert(JSON.stringify( self.allItemsDC_Soto));
      },
      error: function (error) {
            alert(JSON.stringify(error));
      }
    });
   
  },

  SelectedSoTo: function(e){
    //console.log(e);
    pSoto = e;
    var self = this;
    $.ajax({
      url: 'http://igeoserver.herokuapp.com/query/v1/diachinh?columns="soto", "sothua"&filter="soto"%20=%20%27' + pSoto+ '%27&limit=10000',
      type: 'get',
      dataType: 'json',
      async: false,
      success: function(data) {
      self.pSothuas = data;

      var pNewArr1 = _.uniqBy(data, 'sothua');
      var pNewArr2 =  _.map(pNewArr1, 'sothua');
      var pNewArr3 = _.map(pNewArr2, _.unary(parseInt));
      self.pSothuas = _.sortBy(pNewArr3);

      // alert(JSON.stringify(pNewArr1));
      //  alert(JSON.stringify(pNewArr2));
      //  alert(JSON.stringify(pNewArr3));
       // alert(JSON.stringify( self.pSothuas));
      },
      error: function (error) {
            alert(JSON.stringify(error));
      }
    });

  },
  UpdateSoTo(e){
     pSoto = e;
  },

  SelectedSoThua: function(e){
    //console.log(e);
    pSothua = e;
  
  },

  UpdateSoThua(e){
     pSothua = e;
  },

  pQueryDC: function(){
      // this.pOpenResult = false;
      var self = this;
      var pStringFilter = '&filter=';
      var pQuerySoto;
      var pQuerySothua;
      var pQueryDiaChifull;
      var pQueryfull;

     if ($('#checkbox1').checkbox('is checked')){
      //alert("check");
        pQueryDiaChifull = pStringFilter + '"diachifull"%20=%20%27' + pDiaChifull + '%27';
        pQueryfull = pQueryDiaChifull;
     }
     else{
        //alert("uncheck");
        if (pSoto !="" && pSoto != undefined ){
          pQuerySoto = pStringFilter + '"soto"%20=%20%27' + pSoto+ '%27';
        }
         else{
         // alert("chua nhap so thua");
        pQuerySoto="";
        }

        if (pSothua !="" && pSothua != undefined){
          pQuerySothua = 'and"sothua"%20=%20%27' + pSothua+ '%27';
        }
        else{
         // alert("chua nhap so thua");
        pQuerySothua="";
        }
        pQueryfull = pQuerySoto + pQuerySothua;
     }
      
      
      //pQueryfull = pQuerySoto + pQuerySothua;

      var pSucess;
      var pUrl = 'http://igeoserver.herokuapp.com/query/v1/diachinh?columns="soto" as "Số tờ", "sothua" as "Số thửa", "dientich" as "Diện tích", "maloaidat" as "Mã loại đất", "chusd" as "Chủ sử dụng", "diachi", "mdsd" as "Mục đích SD", "tenxa", "tenhuyen", "ghichu" as "Ghi chú", "mathua" as "Mathua", "diachifull"' + pQueryfull + '&limit=10000'
       console.log(JSON.stringify( pUrl));
      $.ajax({
        url: pUrl,
        type: 'get',
        dataType: 'json',
        async: false,
        success: function(data) {
        self.allItemsDC = data;
       
        store.commit('THUADAT', self.allItemsDC);

        // alert("open");
        // // this.pOpenResult = false;
        // this.pOpenResult = true;
        },
        error: function (error) {
              alert(JSON.stringify(error));
        }
      });

        // if (this.pOpenResult == false){
        //  alert("open");
        //   this.pOpenResult = true;
        //   // this.pOpenResult = true;
        // }
       

      // this.pOpenResult = true;
    },


      pQueryDC_P: function(){
        this.pOpenResult = true;
        var self = this;
        $.ajax({
          url: 'http://igeoserver.herokuapp.com/query/v1/diachinh?columns="soto"&filter="tenhuyen"%20=%20%27' + pTenP+ '%27&limit=10000',
          type: 'get',
          dataType: 'json',
          async: false,
          success: function(data) {
          self.allItemsDC_Soto = data;
          
          },
          error: function (error) {
                alert(JSON.stringify(error));
          }
        });
      },



},

  computed: {
  
  }
}
 

</script> 



<style>


.twitter-typeahead .tt-query,
.twitter-typeahead .tt-hint {
  margin-bottom: 0;

}
.tt-menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  display: none;
  float: left;
  min-width: 160px;
  padding: 5px 0;
  margin: 2px 0 0;
  list-style: none;
  font-size: 14px;
  background-color: #ffffff;
  border: 1px solid #cccccc;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  background-clip: padding-box;
}

.tt-hint {
  display: block;
  width: 100%;
  height: 38px;
  padding: 8px 12px;
  font-size: 14px;
  line-height: 1.428571429;
  color: #999;
  vertical-align: middle;
  background-color: #ffffff;
  border: 1px solid #cccccc;
  border-radius: 4px;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  -webkit-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
        transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}
.tt-dropdown-menu {
  width: 100%;
  min-width: 160px;
  margin-top: 2px;
  padding: 5px 0;
  background-color: black;
  border: 1px solid #cccccc;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
        box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  background-clip: padding-box;

}
.tt-suggestion {
  display: block;
  padding: 5px 15px;
   background-color: #ffffff;
  width: 100%;


}
.tt-suggestion.tt-is-under-cursor {
  color: #fff;
  background-color: #428bca;
}
.tt-suggestion.tt-is-under-cursor a {
  color: #fff;
  background-color: black;
}
.tt-suggestion p {
  margin: 0;
  cursor:pointer;

}


.tt-suggestion:hover { 
cursor: pointer; 
color: #fff; 
background-color: #0097cf; 
} 
.tt-suggestion.tt-cursor { 
color: #fff; 
background-color: #0097cf; 
}



#pThongtin::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
#pThongtin::-webkit-scrollbar {
    width: 10px;
   
}
#pThongtin::-webkit-scrollbar-thumb {

  background-color: rgba(0,0,0,0.3);
  outline: 1px solid slategrey;
}

#pThongtin:hover{

}

@import '../../node_modules/semantic-ui-css/semantic.min.css'
/*@import'../../node_modules/vuejs-autocomplete/resources/assets/sass/typeahead_suggestion.scss'*/

</style>



