                //     .setHTML(e.features[0].properties.loaidat + ' ----- ' + e.features[0].properties.dientich)
                //     .addTo(map);

                 // map.setFilter("ranhqhct-fills-hover", ["==", "chieucao", e.features[0].properties.chieucao]);

                 // console.log(e.features[0].properties.chieucao)

                var feature = e.features[0];

                overlay.innerHTML = '';

                var title = document.createElement('strong');
                title.textContent = feature.properties.loaidat + ' (' + feature.properties.loaidat + ' found)';

                var population = document.createElement('div');
                population.textContent = 'Total population: ' +  feature.properties.loaidat;

                overlay.appendChild(title);
                overlay.appendChild(population);
                overlay.style.display = 'block';

                store.commit('QHCHUNG', feature.properties); 
                
               
            });

            map.on('mouseleave', 'qhchung', function() {
                map.getCanvas().style.cursor = '';
                popup.remove();
                // map.setFilter("ranhqhct-fills-hover", ["==", "chieucao", ""]);
                overlay.style.display = 'none';

                store.commit('QHCHUNG', ""); 
            });


            // map.on('mousemove', 'ranhqhct', function(e) {
            //     // Change the cursor style as a UI indicator.
            //     map.getCanvas().style.cursor = 'pointer';

            //     // Populate the popup and set its coordinates
            //     // based on the feature found.
            //     popup.setLngLat(e.lngLat)
            //         .setHTML(e.features[0].properties.tenduan + ' ----- ' + e.features[0].properties.chieucao)
            //         .addTo(map);
            //      map.setFilter("ranhqhct-fills-hover", ["==", "chieucao", e.features[0].properties.chieucao]);

            //      // console.log(e.features[0].properties.chieucao)

               
            // });

            // map.on('mouseleave', 'ranhqhct', function() {
            //     map.getCanvas().style.cursor = '';
            //     popup.remove();
            //     map.setFilter("ranhqhct-fills-hover", ["==", "chieucao", ""]);
            //     // overlay.style.display = 'none';
            // });

        },

  
    },
    mounted: function () {
        this.initMap(); 


        store.watch(store.getters.zkgian, kgian => {
            this.map.getSource('drone').setData(kgian);
            // this.map.fitBounds(geojsonextent(kgian),{padding: 200});
            // this.map.setZoom(this.map.getZoom + 0.5);
            // console.log(kgian)
        });

        store.watch(store.getters.zchonglop, chonglop => {
            this.map.getSource('drone').setData(chonglop);
            this.map.fitBounds(geojsonextent(chonglop),{padding: 200});
            // this.map.setZoom(this.map.getZoom + 0.5);
            // console.log(chonglop)
        });

        // store.watch(store.getters.zthuoctinh, thuoctinh => {
        //        console.log(thuoctinh)
        // });
    }
};
</script>

