<template lang="html">
<div class="ketqua">
  <div style="margin-left:20px;" >

<!-- TITLE -->  
    <form class="ui form attached fluid segment" style="top:34px;padding-bottom:10px; padding-right:10px;"> 
      <a class="ui icon button" style="position: absolute;right:20px;background-color: none;"  href="http://localhost:8080/#/bdqh" ><i class="search icon"></i></a>
     
      <p style="font-size: 22px;text-align: center; font-weight:bold;">Kết quả tìm kiếm</p>
  
<!-- KET QUA TIM KIEM-->

      <div id='container' style='height:450px;width:420px;padding-top:10px;overflow-y:scroll;'>
        <div  v-if="pQHC" >
          <div v-for="Item in thuadats" >

            <div style="margin-top:0px;">  
              <i class="print icon" style="color: #a48800; position:relative; float:right;" v-on:click="ExportPDF" ></i>       
              <i class="info circle icon" style="color: #a48800; position:relative; float:right;" v-on:click="pQueryQHCT(Item.Mathua)" ></i>
              <p style="width:320px; color:#b62020; padding-left:10px; ">{{Item.diachi}}, {{Item.tenxa}}, {{Item.tenhuyen}}</p>
            </div>

            <table class="ui very basic table" style="padding-bottom:40px;" >    
              <thead >                    
                  <th style="margin-top:-10px; background-color:#a5a5a8;padding-bottom:0px;padding-top:0px"></th>
                  <th style="margin-top:-10px;background-color:#a5a5a8;padding-bottom:0px;padding-top:0px"></th>          
                </thead>  
              <tbody>
               <thead v-for="pKey in Object.keys(Item)">                    
                  <th v-if = "pKey != 'diachi' && pKey != 'tenxa' && pKey != 'tenhuyen' && pKey != 'diachifull'" style="font-weight:normal; padding:10px;color:#0380b6; width:150px;">{{ pKey}}</th>
                  <th v-if = "pKey != 'diachi' && pKey != 'tenxa' && pKey != 'tenhuyen' && pKey != 'diachifull'" style="font-weight:normal; padding:10px;color:#626262; width:250px;">{{Item[pKey]}}</th>          
                </thead> 
              </tbody>
            </table> 

          </div>       
           
        </div>
        <div v-else >
              <div v-for="Item in allItemsQHCT" >

            <div style="margin-top:0px;">         
              <i class="info circle icon" style="color: #a48800; position:relative; float:right;" v-on:click="pReturnDC" ></i>
              <p style="width:320px; color:#b62020; padding-left:10px; ">{{"Quy hoạch: " + Item.maqh + " "}}</p>
            </div>

            <table class="ui very basic table" style="padding-bottom:40px;" >    
              <thead >                    
                  <th style="margin-top:-10px; background-color:#a5a5a8;padding-bottom:0px;padding-top:0px"></th>
                  <th style="margin-top:-10px;background-color:#a5a5a8;padding-bottom:0px;padding-top:0px"></th>          
                </thead>  
              <tbody>
               <thead v-for="pKey in Object.keys(Item)">                    
                  <th v-if = "pKey != 'maqh'" style="font-weight:normal; padding:10px;color:#0380b6; width:150px;">{{ pKey}}</th>
                  <th  v-if = "pKey != 'maqh'" style="font-weight:normal; padding:10px;color:#626262; width:250px;">{{Item[pKey]}}</th>          
                </thead> 
              </tbody>
            </table> 

          </div>     

        </div>
      </div>  
<!---->
    
    </form>
 </div>     
 </div> 
</template>

<script>
  
import store from '../../src/store'

import { mapGetters } from 'vuex';

import jspdf from 'jspdf';



var pQHC;
var allItemsQHCT;
export default {
   // extends: VueTypeahead,
  name: 'ketqua',
  data(){
    return{
     
       pQHC,
       allItemsQHCT,
    }
  },
  computed: {
    thuadats: function () {
      // alert(store.state.pThuaDats);
    return store.state.pThuaDats
    },

    // showQHCT: function () {
    //   // alert(store.state.pThuaDats);
    //   return store.state.showQHCT
    // },

    // aItemsQHCT: function () {
    //   // alert(store.state.pThuaDats);
    //   return store.state.pQHCTs
    // }
  },

 mounted: function () {
     
      this.pQHC=true;

      // store.watch(store.getters.zshowQHCT, showQHCT => {
      //   this.pQHC=showQHCT;
      //   // console.log(showQHCT);

      //   // pQueryQHCT(Item.Mathua)
      // })

      store.watch(store.getters.zpQHCTs, pQHCTs => {
        this.pQHC=false;
        this.allItemsQHCT=pQHCTs;
        // console.log(showQHCT);

        // pQueryQHCT(Item.Mathua)
      })
    },

  methods: {
    
  pReturnDC: function(){
    this.pQHC = !this.pQHC;
  },

  ExportPDF: function(){
    //alert("export");

  var doc = new jsPDF();
doc.text(20, 20, 'Hello world!');
doc.text(20, 30, 'This is client-side Javascript, pumping out a PDF.');
doc.addPage();
doc.text(20, 20, 'Do you like that?');

// Output as Data URI
doc.output('datauri');
  },

  pQueryQHCT: function(pMathua){
      // this.pQHC = !this.pQHC;
      this.pQHC = false;
       var self = this;

       // store.commit('KGIAN', pMathua); 

       store.commit('CHONGLOP', pMathua); 
     
      $.ajax({
        url: 'https://igeoserver.herokuapp.com/intersect_feature/v1/qhct/diachinh?geom_column_from=geom&geom_column_to=geom&columns=%22t%22.%22dientich%22 as "Diện tích",%20%22f%22.%22maqh%22,%20%22f%22.%22loaidat%22 as "Loại đất",%20ST_Area(ST_Intersection(%20ST_Transform(%22t%22.%22geom%22,%203405)%20,ST_Transform(%20%22f%22.%22geom%22,%203405)))%20as%20%22DTQH%22&filter=%22t%22.%22mathua%22%20=%20%27' + pMathua + '%27&distance=0&limit=100',
        type: 'get',
        dataType: 'json',
        async: false,
        success: function(data) {
        self.allItemsQHCT = data;        
        // alert(JSON.stringify(self.allItemsQHCT));
        },
        error: function (error) {
              alert(JSON.stringify(error));
        }
      });
      store.commit('MATHUA', pMathua);
    },
  }

  
}
 </script>


