import Vue from 'vue'

import Router from 'vue-router'
import pMap from '@/components/Map'
import Hello from '@/components/Hello'
import Toolbar from '@/components/Toolbar'
import TrangChu from '@/components/TrangChu'
import Ketqua from '@/components/KetquaTimkiem'
import pMapGL from '@/components/MapGL'



//import pMap from '@/components/Map'



Vue.use(Router)


export default new Router({
  routes: [	
  
  {
      path: '/',
     // name: 'Map',
      components: {
      	pmap: pMapGL,      
        TrangChu: TrangChu,
      }
      
      
  		
   },

   {
      path: '/bdqh',
     // name: 'Map',
      components: {
        pmap: pMapGL,
        TrangChu: TrangChu,
        toolbar: Toolbar,

       
      }
   },

   {
      path: '/bdqh/kq',
     // name: 'Map',
      components: {
        pmap: pMapGL,
        TrangChu: TrangChu,
        toolbar: Toolbar,
        kqtk: Ketqua,

       
      }
   },
  
  ]
})



